define('../src/sublayouts/m31-search/m31-search',['jquery', 'slick', 'breakpoint', 'lib/utils'], function($, slick, breakpoint, utils) {

    'use strict';

    var M31_SEARCH = function($component) {

        this.$component = $component;

        return this;
    };

    /**
     * Initialise component
     */

    M31_SEARCH.prototype.init = function() {
        var _this = this,
            $input = _this.$component.find('.m31-search--input'),
            $searchBtn = _this.$component.find('.m31-search--button'),
            $searchCloseBtn = _this.$component.find('.m31-search--dm_close'),
            $searchWordBtn = _this.$component.find('.m31-search--dm_close-word'),
            $searchMobBack = _this.$component.find('.m31-search--back'),
            $searchMic = _this.$component.find('.m31-search--mic');

        _this.setMobile();

        $input.on('keyup paste', function(event) {
            if ($input.val() != '') {
                _this.searchWord(true)
            }
            if ($input.val() == '') {
                _this.searchWord(false)
            }
        });

        $searchWordBtn.on('click', function() {
            _this.searchWord(false)
            _this.$component.find('.m31-search--dm_close').trigger('click');
        })
        $searchMobBack.on('click', function() {
            _this.$component.find('.m31-search--dm_close').trigger('click');
            $searchWordBtn.trigger('click');
        })


        $('body').off('click.m31').on('click.m31', function(event) {
            if (!$(event.target).is('.m31-search--dm_close') && !$(event.target).is('.m31-search--back') && !$(event.target).is('.m31-search--dm_close-word') && $(event.target).closest('.m31-search').length != 0) {
                _this.checkFocus(true);
            }
            if ($(event.target).is('.m31-search--dm_close') || $(event.target).is('.m31-search--back')) {
                _this.checkFocus(false);
            }
        });

        $searchBtn.on('touchstart', function(e) {
            _this.checkFocus(true);
        })


        $(window).on('orientationchange resize', $.proxy(this.setMobile, this));

        $(breakpoint).on('change', $.proxy(this.setMobile, this));

        if (utils.breakpoint() === 'l' || utils.breakpoint() === 'xl') {
            _this.$component.removeClass('m31-search__mobile');
        }

        this.$component.find('.m31-search--expand').off('click.m31').on('click.m31', function() {
            if (utils.breakpoint() == 'l' || utils.breakpoint() == 'xl') {
                $(this).hide();
                _this.$component.find('.m31-search--tools-wrapper').fadeIn();
                _this.$component.find('.m31-search--input').focusin();
            }
        });

        $(document).off('click.m31close').on('click.m31close', function(event) {
            if ((utils.breakpoint() == 'l' || utils.breakpoint() == 'xl') && $(event.target).closest('.m31-search').length == 0 && _this.$component.find('.m31-search--input').val().length == 0) {
                _this.$component.find('.m31-search--tools-wrapper').fadeOut();
                setTimeout(function() {
                    _this.$component.find('.m31-search--expand').show();
                }, 400)
            };
        });

    };

    M31_SEARCH.prototype.checkFocus = function(isFocus) {
        var _this = this,
            $input = _this.$component.find('.m31-search--input'),
            $m12ToolSearch = _this.$component.closest('.m12-tools__search'),
            $m12BarWrapper = _this.$component.closest('.m12-bar__wrapper'),
            $searchDoormat = _this.$component.find('.m31-search--doormat');

        if (isFocus || $searchDoormat.hasClass('active')) {
            _this.$component.addClass('active');
            $m12ToolSearch.addClass('search-active');
            if (_this.$component.hasClass('m31-search__icon--only') || _this.$component.hasClass('m31-search__mobile')) {
                $m12BarWrapper.addClass('search-active-wrapper');
                $input.focus()
            }
        } else {
            _this.$component.removeClass('active');
            $m12ToolSearch.removeClass('search-active');
            $m12BarWrapper.removeClass('search-active-wrapper');
        }
    };


    M31_SEARCH.prototype.setMobile = function() {

        var _this = this,
            dir = $('html').attr('dir') == "ltr" ? 'right' : 'left',
            $m12ToolSearch = _this.$component.closest('.m12-tools__search'),
            $m12BarWrapper = _this.$component.closest('.m12-bar__wrapper'),
            m12BarWrapperWidth = $m12BarWrapper.width(),
            m12MyAccountWidth = $m12BarWrapper.find('.m12-myaccount').width(),
            $input = _this.$component.find('.m31-search--input'),
            isFocus = false,
            totalContentWidth = 0;
        //_this.$component.addClass('m31-search__icon--only');

        $m12BarWrapper.children().each(function() {
            var thisWidth = 0;
            if ($(this).hasClass('m12-tools__search')) {
                thisWidth = 402;
                /*if(_this.$component.hasClass('m31-search__icon--only')){

                }
                if(_this.$component.hasClass('active')){
                    thisWidth = 240;
                }else{
                    thisWidth = $(this).outerWidth(true) + 230;
                }*/
            }
            if ($(this).hasClass('m12-bar__content') || $(this).hasClass('m12-myaccount')) {
                thisWidth = $(this).outerWidth(true);
            }
            totalContentWidth = totalContentWidth + thisWidth
        })


        if (utils.breakpoint() === 's' || utils.breakpoint() === 'm') {
            $m12ToolSearch.css(dir, (0) + 'px')
            _this.$component.addClass('m31-search__mobile');
        } else {
            $m12ToolSearch.css(dir, '')
        }

        if (utils.breakpoint() === 'l' || utils.breakpoint() === 'xl') {
            _this.$component.removeClass('m31-search__mobile');
            if (totalContentWidth >= m12BarWrapperWidth) {
                _this.$component.addClass('m31-search__icon--only');
            }
            if (totalContentWidth < m12BarWrapperWidth) {
                _this.$component.removeClass('m31-search__icon--only');
                $m12BarWrapper.removeClass('search-active-wrapper');
            }
        }

        if ($input.val() || $input.is(':focus') || _this.$component.hasClass('active')) {
            isFocus = true
        }
        _this.checkFocus(isFocus);
    };

    M31_SEARCH.prototype.setWidth = function() {

        var _this = this,
            $m12BarWrapper = _this.$component.closest('.m12-bar__wrapper'),
            $m12QuickLinks = $m12BarWrapper.find('.m12-quicklinks'),
            setWidth = $m12QuickLinks.outerWidth(true) + 240;

        if (_this.$component.hasClass('m31-search__icon--only') && _this.$component.hasClass('active')) {
            _this.$component.find('.m31-search--tools').css('width', setWidth);
            _this.$component.find('.m31-search--input').css('width', setWidth);
        }
        if (!_this.$component.hasClass('active')) {
            _this.$component.find('.m31-search--tools').css('width', '');
            _this.$component.find('.m31-search--input').css('width', '');
        }
    }


    M31_SEARCH.prototype.searchWord = function(active) {

        var _this = this,
            $input = _this.$component.find('.m31-search--input'),
            $searchBtn = _this.$component.find('.m31-search--button'),
            $closeWorldBtn = _this.$component.find('.m31-search--dm_close-word');

        if (active) {
            $searchBtn.hide()
            $closeWorldBtn.show()
        } else {
            $searchBtn.show()
            $closeWorldBtn.hide()
            $input.val('')
        }
    }




    return M31_SEARCH;
});


