define('../src/sublayouts/m28-dashboard-component-gauge/m28-dashboard-component-gauge',['jquery', 'lib/utils', 'tooltipster', 'chart-config'], function($, utils, tooltipster) {

    'use strict';

    var gaugeDashboardComponent = function($component) {

        this.$component = $component;

        return this;
    };

    gaugeDashboardComponent.prototype.init = function() {
        var _this = this,
            data = JSON.parse(this.$component.attr('data-chart-data')),
            months = JSON.parse(this.$component.attr('data-months')),
            edata = data.electricity,
            wdata = data.water,
            cdata = data.carbonFootprint;

        //if ( edata !=null && edata.length > 0 ) {
        //    this.initColumnGraph(edata, JSON.parse(this.$component.attr('data-electricity-max')), '.m28-columncharts--electricity', '.m28-gauge--container--electricity');    
        //};
        //if ( wdata!=null && wdata.length > 0 ) {
        //    this.initColumnGraph(wdata, JSON.parse(this.$component.attr('data-water-max')), '.m28-columncharts--water', '.m28-gauge--container--water');
        //};

        if ( edata !=null && edata.length > 0 ) {
            this.initGaugeGraph(4, edata[edata.length - 1], JSON.parse(this.$component.attr('data-electricity-max')), '.m28-gauge--container--electricity', 76.25);
        };
        if ( wdata !=null && wdata.length > 0 ) {
            this.initGaugeGraph(3, wdata[wdata.length - 1], JSON.parse(this.$component.attr('data-water-max')), '.m28-gauge--container--water', 106.666667);
        };
        if ( cdata!=null && cdata.value != undefined ) {
            this.initGaugeGraphCarbon(4, cdata, JSON.parse(this.$component.attr('data-carbonfootprint-max')), '.m28-gauge--container--carbonfootprint', 76.25);
        };

        //this.initLineGraph(wdata, '.m28-columncharts--water', JSON.parse(this.$component.attr('data-water')));
        //this.initLineGraph(edata, '.m28-columncharts--electricity', JSON.parse(this.$component.attr('data-electricity')));
        

        jQuery(window).off('reinit_m28').on('reinit_m28', function() {
            _this.init();
        });

    };

    gaugeDashboardComponent.prototype.initGaugeGraph = function(bars, rawvalue, max, placeholder, per) {
        var value = parseFloat(rawvalue.value),
            base = parseFloat(max),
            month = rawvalue.month,
            year = rawvalue.year,
            percentage = (base / bars),
            rate = per / percentage,
            bar_count = parseInt(value / percentage) + 1,
            excess_rate = (value - ((bar_count - 1) * percentage)) * rate,
            months = JSON.parse(this.$component.attr('data-months')),
            min_bar = 1;

        while (min_bar < bar_count) {
            var $g = $(placeholder).find('.m28-gauge--bar' + min_bar + '');

            $g.find('#m28-gauge--bar_fill').css({
                strokeDasharray: per.toString() + ' 9999',
                opacity: '1'
            });

            min_bar++;
        };

        if (excess_rate > 0) {
            $(placeholder).find('.m28-gauge--bar' + (min_bar) + '').find('#m28-gauge--bar_fill').css({
                strokeDasharray: excess_rate.toString() + ' 9999',
                opacity: '1'
            });   
        };

        $(placeholder).closest('.m28-gauge--wrapper').find('.m28-gauge--details-value').html(rawvalue.value);
        $(placeholder).closest('.m28-gauge--wrapper').find('.m28-gauge--details-month').html(months[parseFloat(month) - 1]);
        $(placeholder).closest('.m28-gauge--wrapper').find('.m28-gauge--details-year').html(year);

        if ( /Edge\/|Trident\/|MSIE /.test(window.navigator.userAgent) ) {
            $('[id=m28-gauge--bar_fill]').each(function(){
                var __this = this;
                $(this).attr('class', '');
                setTimeout(function() {
                    $(__this).attr('class', 'm28-gauge--bar_fill');
                }, 125)
            });
        };

    };

    gaugeDashboardComponent.prototype.initGaugeGraphCarbon = function(bars, rawvalue, max, placeholder, per) {
        var value = parseFloat(rawvalue.value)>500 ? parseFloat(rawvalue.value)-500 : parseFloat(rawvalue.value),
            base = parseFloat(rawvalue.value)>500 ? parseFloat(max)-500 : parseFloat(max),
            month = rawvalue.month,
            year = rawvalue.year,
            percentage = (base / (parseFloat(rawvalue.value)>500 ? bars-1 : bars)),
            rate = per / percentage,
            bar_count = parseInt(value / percentage) + 1,
            excess_rate = (value - ((bar_count - 1) * percentage)) * rate,
            months = JSON.parse(this.$component.attr('data-months')),
            min_bar = 1;

        if (parseFloat(rawvalue.value)>500) {
            while (min_bar < bar_count+1) {
                var $g = $(placeholder).find('.m28-gauge--bar' + min_bar + '');

                $g.find('#m28-gauge--bar_fill').css({
                    strokeDasharray: per.toString() + ' 9999',
                    opacity: '1'
                });

                min_bar++;
            };

            if (excess_rate > 0) {
                $(placeholder).find('.m28-gauge--bar' + (min_bar) + '').find('#m28-gauge--bar_fill').css({
                    strokeDasharray: excess_rate.toString() + ' 9999',
                    opacity: '1'
                });   
            };
        } else {
            $(placeholder).find('.m28-gauge--bar' + (min_bar) + '').find('#m28-gauge--bar_fill').css({
                strokeDasharray: (parseFloat(rawvalue.value)*(per/500)).toString() + ' 9999',
                opacity: '1'
            });
        }

        $(placeholder).closest('.m28-gauge--wrapper').find('.m28-gauge--details-value').html(rawvalue.value);
        $(placeholder).closest('.m28-gauge--wrapper').find('.m28-gauge--details-month').html(months[parseFloat(month) - 1]);
        $(placeholder).closest('.m28-gauge--wrapper').find('.m28-gauge--details-year').html(year);

        if ( /Edge\/|Trident\/|MSIE /.test(window.navigator.userAgent) ) {
            $('[id=m28-gauge--bar_fill]').each(function(){
                var __this = this;
                $(this).attr('class', '');
                setTimeout(function() {
                    $(__this).attr('class', 'm28-gauge--bar_fill');
                }, 125)
            });
        };

    };

    gaugeDashboardComponent.prototype.initColumnGraph = function(data, base, placeholder, unitP) {
        var value,
            unit = $(unitP).next('.m28-gauge--details').find('.m28-gauge--details-unit').html(),
            gdata = 0,
            fbase = base,
            months = JSON.parse(this.$component.attr('data-months'));

        $(placeholder).html('');

        $(data).each(function() {
            if ( this.value > gdata ) {
                gdata = this.value;
            };
        });

        jQuery(data).each(function() {
            var month = this.month,
                rate = ((this.value / (gdata + (gdata*.05))) * 100).toString() + "%";

            $(placeholder).append('<div class="m28-columncharts--item" chart-value="'+this.value+' '+unit+'"><div class="m28-columncharts--title">' + months[parseFloat(month) - 1] + '</div><div class="m28-columncharts--column"><div class="m28-columncharts--columnfill" data-fill="' + rate + '"></div></div></div>')
        });

        setTimeout(function() {
            $(placeholder).find('.m28-columncharts--columnfill').each(function() {
                $(this).css('height', $(this).attr('data-fill'));
                $(this).closest('.m28-columncharts--item').tooltipster({
                    content: $(this).closest('.m28-columncharts--item').attr('chart-value'),
                    maxWidth: 250,
                    arrow: false
                });
            });
        }, 100);

    };

    gaugeDashboardComponent.prototype.initLineGraph = function(data, placeholder, rawtype) {
        var series = [],
            yAxisTitleText = this.$component.data('y-axis-text'),
            xAxisCategories = JSON.parse(this.$component.attr('data-months')),
            tooltipValueSuffix = this.$component.data('tooltip-value-suffix'),
            isRTL = utils.isRTL(),
            type = rawtype.type,
            legend = this.$component.data('legend'),
            label = this.$component.data('label'),
            labelstyle = this.$component.data('labelstyle'),
            labelXAxisStep = parseInt(1, 10) || 1,
            labelXAxisStepMobile = parseInt(2, 10) || 1;

            for (var i in data) {
                series.push(data[i].value);
            };

            var groupBy = function(xs, key) {
              return xs.reduce(function(rv, x) {
                (rv[x[key]] = rv[x[key]] || []).push(x);
                return rv;
              }, {});
            };

            var raw = groupBy(data, 'year'),
                raw1 = [];

            for (var j in raw) {
                var rawdata = []

                for (var k in raw[j]) {
                    rawdata.push(raw[j][k].value);
                };

                raw1.push({'name':j, 'color': raw[j][0].color, 'data':rawdata});
            };

            //console.log(raw1)

            var data = raw1;

            var opts = {
                chart: {
                    type: 'line',
                    scrollablePlotArea: {
                        minWidth: 1000,
                        scrollPositionX: 1
                    }
                },

                title: {
                    text: ''
                },

                subtitle: {
                    text: '',
                    style: {
                        display: 'none'
                    }
                },

                legend: {
                    title: {
                        text: 'SHOWING'
                    },
                    align: 'left',
                    verticalAlign: 'top',
                    borderWidth: 0
                },

                plotOptions: {
                    spline: {
                        marker: {
                            radius: 4,
                            lineColor: '#666666',
                            lineWidth: 1
                        }
                    }

                },
                xAxis: {
                    reversed: isRTL,
                    categories: xAxisCategories,
                    labels: {
                        autoRotation: false,
                        staggerLines: labelXAxisStep
                    }
                },
                yAxis: {
                    opposite: isRTL,
                    title: {
                        text: rawtype.type+' '+rawtype.unit
                    },
                    plotLines: [{
                        value: 0,
                        width: 1,
                        color: '#808080'
                    }]
                },
                tooltip: {
                    headerFormat: '<span style="font-size: 12px;font-weight:bold;">' + type + '</span><br><span style="color:{point.color}">\u25CF</span> {point.x} {series.name}: ',
                    pointFormat: '<b>{point.y}</b>',    
                    valueSuffix: rawtype.unit,
                    crosshairs: true,
                    useHTML: true
                },

                series: data
            };

        if (utils.breakpoint() === 's') {
            opts.xAxis.labels.staggerLines = labelXAxisStepMobile;
        }

        $(placeholder).highcharts(opts);
    };

    return gaugeDashboardComponent;

});

