define('../src/sublayouts/m74-rammas_expand/m74-rammas_expand',['jquery', 'lib/utils'], function($, utils) {

    'use strict';

    var M74_RE = function($component) {

        this.$component = $component;

        return this;
    };

    /**
     * Initialise component
     */

    M74_RE.prototype.init = function() {
        var _this = this,
            $button = this.$component.find('.m74-rammas_expand--icon'),
            $content = this.$component.find('.m74-rammas_expand--container');

        this.size();

        $(window).off('resize.m74').on('resize.m74', function() {
            _this.size();
        });

        $button.off('click').on('click', function() {
            $content.toggleClass('m74-rammas_expand--container_active');
            $button.toggleClass('m74-rammas_expand--icon_active');
        });

        if (this.$component.hasClass('m74-rammas_expand--auto') && !this.getCookie()) {
            setTimeout(function() {
                $button.click();
            }, 1500);
            _this.$component.find('.m74-rammas_expand--close').off('click').on('click', function() {
                _this.$component.find('.m74-rammas_expand--icon_active').click();
                _this.setCookie()
            });
            _this.$component.find('.m74-rammas_expand--link').off('click').on('click', function() {
                _this.setCookie()
            });
        };

        if (this.$component.hasClass('m74-rammas_expand--onlinesupport')) {
            var $button = _this.$component.find('.m74-rammas_expand--os_button'),
                $iframelink = _this.$component.find('.m74-rammas_expand--os_iframelink'),
                $iframeback = _this.$component.find('.m74-rammas_expand--os_iframeback');

            $button.off('click.m74').on('click.m74', function() {
                $button.toggleClass('active');
                _this.$component.find('.m74-rammas_expand--os_list').toggleClass('active');
                _this.$component.find('.m74-rammas_expand--os_iframe').toggleClass('active');
                _this.$component.find('.m74-rammas_expand--os_overlay').toggleClass('active');
                $('body').toggleClass('unscrollable');
            });

            $button.off('keyup.m74').on('keyup.m74', function(event) {
                if (event.keyCode == 13) {
                    $button.toggleClass('active');
                    _this.$component.find('.m74-rammas_expand--os_list').toggleClass('active');
                    _this.$component.find('.m74-rammas_expand--os_iframe').toggleClass('active');
                    _this.$component.find('.m74-rammas_expand--os_overlay').toggleClass('active');
                    $('body').toggleClass('unscrollable');
                }
            });

            _this.$component.off('keyup.closem74').on('keyup.closem74', function(event) {
                if (event.keyCode == 27) {
                    $button.removeClass('active');
                    _this.$component.find('.m74-rammas_expand--os_list').removeClass('active');
                    _this.$component.find('.m74-rammas_expand--os_iframe').removeClass('active');
                    _this.$component.find('.m74-rammas_expand--os_overlay').removeClass('active');
                    $('body').removeClass('unscrollable');
                };
            });

            _this.$component.find('.m74-rammas_expand--os_overlay').off('click.m74').on('click.m74', function() {
                $button.toggleClass('active');
                _this.$component.find('.m74-rammas_expand--os_list').toggleClass('active');
                _this.$component.find('.m74-rammas_expand--os_iframe').toggleClass('active');
                _this.$component.find('.m74-rammas_expand--os_overlay').toggleClass('active');
                $('body').toggleClass('unscrollable');
            });

            $iframelink.each(function() {
                $(this).off('click.m74').on('click.m74', function() {
                    _this.$component.find('.m74-rammas_expand--os_iframe iframe').attr('src', $(this).attr('data-iframe'));
                    _this.$component.find('.m74-rammas_expand--os_iframe').addClass('iframe');
                    _this.$component.find('.m74-rammas_expand--os_list').addClass('iframe');
                });
                $(this).off('keyup.m74').on('keyup.m74', function(event) {
                    if (event.keyCode == 13) {
                        _this.$component.find('.m74-rammas_expand--os_iframe iframe').attr('src', $(this).attr('data-iframe'));
                        _this.$component.find('.m74-rammas_expand--os_iframe').addClass('iframe');
                        _this.$component.find('.m74-rammas_expand--os_list').addClass('iframe');
                    }
                });
            });

            $iframeback.off('click.m74').on('click.m74', function() {
                _this.$component.find('.m74-rammas_expand--os_iframe').removeClass('iframe');
                _this.$component.find('.m74-rammas_expand--os_list').removeClass('iframe');
            });

            $iframeback.off('keyup.m74').on('keyup.m74', function(event) {
                if (event.keyCode == 13) {
                    _this.$component.find('.m74-rammas_expand--os_iframe').removeClass('iframe');
                    _this.$component.find('.m74-rammas_expand--os_list').removeClass('iframe');
                };
            });
        };

    };

    M74_RE.prototype.setCookie = function() {
        document.cookie = 'dewa_74=1;path=/; expires=' + this.$component.attr('data-expiry');
    };

    M74_RE.prototype.getCookie = function() {
        var nameEQ = "dewa_74=";

        var ca = document.cookie.split(';');

        for (var i = 0; i < ca.length; i++) {

            var c = ca[i];

            while (c.charAt(0) == ' ') {
                c = c.substring(1, c.length)
            };

            if (c.indexOf(nameEQ) == 0) {
                return true
            };

        }

        return null;
    };

    M74_RE.prototype.size = function() {
        var _this = this,
            $content = this.$component.find('.m74-rammas_expand--container'),
            $height = ($(window).height()),
            $contentHeight = $content.outerHeight(true) + 80;

        if (($contentHeight > $height) && (utils.breakpoint() === 's' || utils.breakpoint() === 'm')) {
            $content.addClass('m74-rammas_expand--container_LS');
        } else {
            $content.removeClass('m74-rammas_expand--container_LS');
        };
    };

    return M74_RE;
});

