define('../src/sublayouts/m47-video-gallery/m47-video-gallery',['jquery', 'lib/utils', 'jquery_mobile_touch'], function ($, utils) {

	'use strict';

	var VideoCarousel = function ($component) {

		this.$component = $component;

		return this;
	};

	VideoCarousel.prototype.init = function () {
		var tag = document.createElement('script'),
			firstScriptTag = document.getElementsByTagName('script')[0],
			_this = this;

		window.player = null;
		_this.isRTL = $('html').attr('dir') == 'rtl';
		_this.$sliderContainer = _this.$component.find('.m47-video-gallery__thumb-gallery-container');
		_this.$slider = _this.$component.find('.m47-video-gallery__thumb-gallery');
		_this.$numSlides = _this.$component.find('.m47-video-gallery__thumb-gallery-slide').length;
		_this.$slidesToShow = 6;
		_this.$gutter = ($( window).width() >= 1024) ? 8 : 4;
		_this.$padding = ($( window).width() >= 1024) ? 0 : 32;
		_this.$slideImgs = _this.$component.find('.m47-video-gallery__thumb-gallery-slide-image');
		_this.$slides = _this.$component.find('.m47-video-gallery__thumb-gallery-slide');
		_this.$inner = _this.$component.find('.m47-video-gallery__thumb-gallery--inner');
		_this.$slidetrack = _this.$component.find('.m47-video-gallery__thumb-gallery--track');
		_this.$videoContainer = _this.$component.find('.m47-video-gallery__videoplayer-container');


		tag.src = "//www.youtube.com/iframe_api";
		firstScriptTag.parentNode.insertBefore(tag, firstScriptTag);

		window.initID = _this.$component.find('[data-index="0"]').data('videoid');


		//_this.$videoContainer.height(_this.$videoContainer.width()/16*9);

		_this.initCarousel();
		$('.m47-video-gallery__thumb-gallery-slide').each(function (e) {
			$(this).attr('tabindex', '0')
			//console.log('asd')
		})
	};

	VideoCarousel.prototype.initCarousel = function () {
		var _this = this;
		this.$slideImgs.each(function(){
			$(this).css({
				background: 'url("'+$(this).data('src')+'")',
				backgroundSize: 'auto 150%',
				backgroundPosition: 'center center',
				backgroundRepeat: 'no-repeat'
			})
		});

		// add cloned slides to the beginning of the track
		for(var n=(this.$slides.length-1); n > ((this.$slides.length-1)-this.$slidesToShow); n--){
			this.$slidetrack.prepend($(this.$slides[n]).clone(true));
		}
		// add cloned slides to the end of the track
		for(var n=0; n < this.$slidesToShow; n++){
			this.$slidetrack.append($(this.$slides[n]).clone(true));
		}

		//update slides length
		this.$slides = $('.m47-video-gallery__thumb-gallery-slide');

		// work out display width
		this.$imageWidth = Math.round(((this.$inner.width() - ((this.$slidesToShow-1) * this.$gutter)) / this.$slidesToShow));

		this.$tileWidth = this.$imageWidth + this.$gutter;

		this.$slidetrack.width(this.$slides.length * this.$tileWidth);

		// add tile padding
		this.$slides.find('.m47-video-gallery__thumb-gallery-slide-image').each(function () {
			$(this).css({
				width: _this.$imageWidth,
				paddingBottom: _this.$imageWidth
			});
		});

		this.$slides.find('.m47-video-gallery__thumb-gallery-slide-inner').each(function () {
			$(this).css({
				paddingRight: _this.$gutter
			});
		});

		var sl = this.$slides.length;
		var tw = this.$tileWidth;
		var sts = _this.$slidesToShow;
		this.$initOffset = _this.isRTL ? ((tw*sl-((tw*sts)*2))*-1) :((tw*sts)*-1);
		this.$slidetrack.css('position', 'absolute');
		this.$inner.css('height', $(this.$slidetrack).height());

		this.$slidetrack.css('left', this.$initOffset);

		//this.$slidetrack.css('transition', 'left 0.5s');

		this.$inner.append('<div role="button" aria-label="Previous" class="btn-prev"></div>');
		this.$inner.append('<div role="button" aria-label="Next" class="btn-next"></div>');

		this.$slidetrack.on('swiperight', function(e){
			e.stopImmediatePropagation();
			e.preventDefault();
			_this.scrollCarousel(_this.isRTL ? 1 : -1);
		});

		this.$slidetrack.on('swipeleft', function(e){
			e.stopImmediatePropagation();
			e.preventDefault();
			_this.scrollCarousel(_this.isRTL ? -1 : 1);
		});

		this.$component.find('.btn-prev').unbind().on('click', function (e) {
			e.stopImmediatePropagation();
			e.preventDefault();
			e.stopPropagation();
			_this.scrollCarousel(-1);
		});

		this.$component.find('.btn-next').unbind().on('click', function (e) {
			e.stopImmediatePropagation();
			e.preventDefault();
			e.stopPropagation();
			_this.scrollCarousel(1);
		});

		$(window).on('resize', function () {
			var currentPos = Math.round(($(_this.$slidetrack)[0].offsetLeft * -1) / _this.$tileWidth),
			 	st  = $(_this.$slidetrack),
				i   = $(_this.$inner),
				sts = _this.$slidesToShow,
				s   = $(_this.$slides),
				g   = _this.$gutter = ($( window).width() >= 1024) ? 32 : 16;

			var iw = Math.round(((i.width() - ((sts-1) * g)) / sts));
			var tw =  _this.$tileWidth = iw + g;

			st.width(s.length * tw);

			i.css('height', st.height());

			s.find('.m47-video-gallery__thumb-gallery-slide-image').each(function () {
				$(this).css({
					width: iw,
					paddingBottom: iw
				});
			});

			s.find('.m47-video-gallery__thumb-gallery-slide-inner').each(function () {
				$(this).css({
					paddingRight: _this.$gutter
				});
			});

			st.css({
				left: ((tw*currentPos)*-1)+'px'
			});
		});

		$(this.$slidetrack).bind('webkitTransitionEnd transitionend msTransitionEnd oTransitionEnd', function () {
			_this.transitionComplete();
		});

		//$('.m47-video-gallery__thumb-gallery-slide').on('click', function (e) {
		//	console.log('click');
		//	window.player.loadVideoById($(this).data('videoid'));
		//});

		this.$current=0;
	};

	VideoCarousel.prototype.transitionComplete = function () {
		var _this = this,
			io  = this.$initOffset,
			c   = this.$current,
			st  = $(this.$slidetrack),
			sts = this.$slidesToShow,
			ns  = this.$numSlides,
			tw  = this.$tileWidth;
		//console.log('asd')
		if (this.$current == ((sts)*-1)) {
			c = this.$current = ((ns)-sts);
			st.css({
				left: ((!_this.isRTL) ? ((io-((c)*tw))) : ((io+((c)*tw))) )+'px'
			});
		} else if(c == ns){
			this.$current = 1;
			st.css({
				left: io+'px'
			});
		}

	};

	VideoCarousel.prototype.scrollCarousel = function (dir) {
		var _this = this;
		this.$current += dir;
		var tw = this.$tileWidth,
			sl = $(this.$slidetrack)[0].offsetLeft,
			st = $(this.$slidetrack),
			val = Math.floor(this.isRTL ? sl+(tw*dir) : sl+(tw*(dir*-1)));

		st.animate({
			left: val
		},500, $.proxy(
			function(){
				_this.transitionComplete();
			},
			_this)
		);
	};

	window.onYouTubeIframeAPIReady = function () {
		window.player = new YT.Player('player', {
			width:"100%",
			height:"100%",
			videoId: window.initID,
			events: {
				'onReady': window.onPlayerReady,
				'onStateChange': window.onPlayerStateChange,
				'onError': window.onPlayerError
			}
		});
	};



	window.onPlayerReady = function () {
		player.setPlaybackQuality('large');
		jQuery('.m47-video-gallery__thumb-gallery-slide').on('click', function (e) {
			player.loadVideoById(jQuery(this).data('videoid'));
		});
		jQuery('.m47-video-gallery__thumb-gallery-slide').bind('keyup',function(e){

			if( e.keyCode === 13 ){
				player.loadVideoById(jQuery(this).data('videoid'));
			};
		});
		
	};

	window.onPlayerError = function (e) {
		//console.log(e.data);
	};

	VideoCarousel.prototype.loadVideoById = function (id) {
		window.player.loadVideoById(id);
	};

	window.onPlayerStateChange = function (e) {
		//switch (e.data) {
		//	case YT.PlayerState.UNSTARTED:
		//		console.log('unstarted');
		//		break;
		//	case YT.PlayerState.ENDED:
		//		console.log('ended');
		//		break;
		//	case YT.PlayerState.PLAYING:
		//		console.log('playing');
		//		break;
		//	case YT.PlayerState.PAUSED:
		//		console.log('paused');
		//		break;
		//	case YT.PlayerState.BUFFERING:
		//		console.log('buffering');
		//		break;
		//	case YT.PlayerState.CUED:
		//		console.log('video cued');
		//		break;
		//}
	};

	VideoCarousel.prototype.stopVideo = function () {
		this.player.stopVideo();
	};

	VideoCarousel.prototype.display = function () {
		this.$slider.css({
			top: 0,
			position: 'relative'
		});
	};


	return VideoCarousel;
});

