        $(window).bind('proprogress',function(e,thisVal) {
            $progressbar.attr('data-satus',thisVal);
            $progressbar.data('satus',thisVal);
            var statusBar = $progressbar.find('.progress-status'),
                statusBarNum = statusBar.find('.progress-status-number');
            statusBar.css('width',thisVal +'%');
            statusBarNum.empty().append(thisVal+'%');
            if(thisVal < 8){
                statusBarNum.addClass('progress-status-number-small')
            }else{
                statusBarNum.removeClass('progress-status-number-small')
            }
            if(thisVal==100){
                statusBar.addClass('progress-completed')
            }else{
                statusBar.removeClass('progress-completed')  
            }


          });


