define('../src/journeys/j69-dashboard/j69-dashboard',['jquery', 'slick', 'chart-config'], function ($, slick) {

	'use strict';

	var J69 = function ($journey) {
		dewaGlobal.dashboardJourney = $journey;
		this.$journey = $journey;
		return this;

	};

	J69.prototype.init = function () {
		$(this.$journey).find('.grid').addClass('j69-dashboard__charts');
		this.$clone = $(this.$journey).find('.grid').clone(true, true);
		$(this.$clone).addClass('j69-dashboard__charts--carousel');

		var jo = $(this.$journey).find('j69-dashboard__charts');
		$(this.$journey).append(this.$clone);
		this.plotCharts();
		this.initMobileCarousel();
	};

	J69.prototype.initMobileCarousel = function () {
		var _this = this;
		_this.$slider = $(this.$clone).find('.grid__row');
		_this.$slider.on('init', function(event){
			window.setTimeout(function () { // hide on desktop
				if ($(window).outerWidth() < 600) {
					_this.$slider.css('top', '0');
				}
			}, 200);
		});

		$(this.$clone).find('.grid__row').slick({
			arrows: false,
			dots: true,
			infinite: true,
			speed: 750,
			slidesToShow: 1,
			slidesToScroll: 1,
			autoplay: false,
			autoplaySpeed: 3000,
			pauseOnHover: false,
			rtl: ($('html').attr('dir') == 'rtl')
		});
		
		dewaGlobal.cloneB = _this;
            dewaGlobal.updateDashboardChart = function () {
                $(dewaGlobal.dashboardJourney).find('.grid.j69-dashboard__charts.j69-dashboard__charts--carousel').remove();
                dewaGlobal.clone = $(dewaGlobal.dashboardJourney).find(".grid.j69-dashboard__charts").clone(!0, !0)[0];
                $(dewaGlobal.clone).addClass("j69-dashboard__charts--carousel");
                $(dewaGlobal.dashboardJourney).append(dewaGlobal.clone);
                dewaGlobal.cloneB.$slider = $(dewaGlobal.clone).find(".grid__row");
                dewaGlobal.cloneB.$slider.on("init", function (c) {
                    window.setTimeout(function () {
                        $(window).outerWidth() < 600 && dewaGlobal.cloneB.$slider.css("top", "0");
                        //dewaGlobal.refreshDashboardChartSlider();
                    }, 200)
                });
                $(dewaGlobal.clone).find(".grid__row").slick({
                    arrows: !1,
                    dots: !0,
                    infinite: !0,
                    speed: 750,
                    slidesToShow: 1,
                    slidesToScroll: 1,
                    autoplay: !1,
                    
                    autoplaySpeed: 3e3,
                    pauseOnHover: !1,
                    rtl: "rtl" == $("html").attr("dir")
                });
                // On before slide change
                $(dewaGlobal.clone).find(".grid__row").on('beforeChange', function (event, slick, currentSlide, nextSlide) {
                    window.setTimeout(function () {
                        $(window).outerWidth() < 600 && dewaGlobal.cloneB.$slider.css("top", "0");
                        dewaGlobal.refreshDashboardChartSlider();
                    }, 200)
                });
                $(dewaGlobal.clone).find(".grid__row.slick-initialized.slick-slider").on("click", function () {
                    window.setTimeout(function () {
                        $(window).outerWidth() < 600 && dewaGlobal.cloneB.$slider.css("top", "0");
                        dewaGlobal.refreshDashboardChartSlider();
                    }, 200)
                });
                return;
            };
            dewaGlobal.refreshDashboardChartSlider = function () {
                if ($(window).outerWidth() < 600) {
                    var dashboardChartClone = $(dewaGlobal.dashboardJourney).find('.grid.j69-dashboard__charts.j69-dashboard__charts--carousel');
                    $(dashboardChartClone.find('.m28-dashboard-component__chart')).each(function (index, element) {
                        var svg = $(this).find('svg')[0];
                        var svgFontSize = parseInt($(svg).css('font-size')) - 1;
                        $(svg).css('font-size', svgFontSize);
                    });
                }
            };
	};

	J69.prototype.plotCharts = function () {

		$('body').find('.m28-dashboard-component').each(function () {

			var chartData = $(this).data('chart-data'),
				colorDown = '#087B36',
				colorUp = '#0068BD',
				data = chartData.split(',').map(function (chartData) {
					return Number(chartData);
				}),
				color = chartData[0] > chartData[chartData.length - 1] ? colorUp : colorDown;

			var $chartContainer = $(this).find('[data-chart-container]');

			$chartContainer.highcharts({
				tooltip: {
					text: '',
					style: {
						display: 'none'
					}
				},
				title: {
					text: '',
					style: {
						display: 'none'
					}
				},
				subtitle: {
					text: '',
					style: {
						display: 'none'
					}
				},
				xAxis: {
					lineWidth: 0,
					minorGridLineWidth: 0,
					lineColor: 'transparent',
					minorTickLength: 0,
					tickLength: 0,
					labels: {
						enabled: false
					},
					title: {
						text: null
					},
					reversed: $('html').attr('dir')=="rtl"
				},
				yAxis: {
					gridLineWidth: 0,
					minorGridLineWidth: 0,
					labels: {
						enabled: false
					},
					title: {
						text: null
					},
					opposite: $('html').attr('dir')=="rtl"
				},

				series: [{
					showInLegend: false,
					data: data,
					color: color
				}]
			});

		});

	};

	return J69;
});
