define('../src/sublayouts/m29v2-chart/m29v2-chart',['jquery', 'lib/utils', 'vendor/jquery.monthpicker', 'chart-config'],
    function($, utils, datepicker) {

        'use strict';

        var ChartV2 = function($component) {

            this.$component = $component;
        };

        ChartV2.prototype.init = function() {
        	var _this = this;

            //this.renderGraph();
            this.toggles();
            this.listeners();

            this.$component.off('m29v2-chart-init').on('m29v2-chart-init', function() {
            	_this.renderGraph();
            });

            this.$component.off('m29v2-chart-nodata').on('m29v2-chart-nodata', function() {
                _this.$component.find('.m29v2-chart__nodata-container').show();
                _this.$component.find('.m29v2-chart__container').hide();
                _this.$component.find('.m29v2-chart__label').hide();
            });

            $('.form-field__input--monthpicker').each(function() {
                var _this = this;

                $(this).datepicker({
                    language: utils.isRTL() ? 'ar' : 'en',
                    minView: 'months',
                    view: 'months',
                    autoClose: true,
                    closeButton: true,
                    maxDate: new Date(),
                    position: utils.isRTL() ? 'bottom right' : 'bottom left',
                    dateFormat: 'MM yyyy',
                    onSelect: function(date) {
                        $(_this).trigger('change');

                        var dat = new Date(date)
                        $(_this).attr('data-month', dat.getMonth())
                        $(_this).attr('data-year', dat.getFullYear())
                    }
                });
            });
        };

        ChartV2.prototype.listeners = function() {
            var _this = this;

            $('.m29v2-chart--fakecbox').off('click.box').on('click.box', function() {
                var $input = $(this).find('input')
                $(this).toggleClass('active');

                if ($(this).hasClass('active')) {
                    $input.prop('checked', true);
                } else {
                    $input.prop('checked', false);
                }
            });

            this.$component.find('.m29v2-chart--typeitem').each(function() {
                $(this).on('click', function() {
                    _this.$component.find('.m29v2-chart--typeitem').each(function() {
                        $(this).removeClass('m29v2-chart--typeitemactive')    
                    });

                    if ( !$(this).hasClass('m29v2-chart--typeitemactive') ) {
                        $(this).addClass('m29v2-chart--typeitemactive');
                       _this.renderGraph();
                    };
                    
                });
            });

        };

        ChartV2.prototype.toggles = function() {
            var _this = this,
                $tab = this.$component.find('.m29v2-chart--listitem'),
                $controls = this.$component.find('.m29v2-chart--controls');

            $tab.each(function() {
                var index = $(this).attr('data-index');

                $(this).off('click.tab').on('click.tab', function() {
                    $controls.each(function() {
                        if ( $(this).attr('data-index') == index ) {
                            if (!$(this).hasClass('active')) {
                                $(this).addClass('active'); 
                            }  
                        } else {
                            $(this).removeClass('active');
                        }
                    });

                    $tab.each(function() {
                        $(this).removeClass('active');
                    });

                    $(this).addClass('active');
                    //_this.renderGraph();
                });
            });
        };

        ChartV2.prototype.renderGraph = function() {
            this.$component.find('.m29v2-chart__nodata-container').hide();
            this.$component.find('.m29v2-chart__container').show();
            this.$component.find('.m29v2-chart__label').show();

            var $container = this.$component.find('[data-chart-container]'),
                series = JSON.parse(this.$component.attr('data-series')),
                yAxisTitleText = this.$component.attr('data-y-axis-text'),
                xAxisCategories = this.$component.attr('data-x-axis-categories').split(','),
                tooltipValueSuffix = this.$component.attr('data-tooltip-value-suffix'),
                isRTL = utils.isRTL(),
                usageFor = this.$component.attr('data-usagefor'),
                legend = this.$component.attr('data-legend'),
                label = this.$component.attr('data-label'),
                labelstyle = this.$component.attr('data-labelstyle'),
                labelXAxisStep = parseInt(this.$component.attr('data-step'), 10) || 1,
                labelXAxisStepMobile = parseInt(this.$component.attr('data-step-s'), 10) || 1,
                opts = {
                    chart: {
                        type: this.$component.find('.m29v2-chart--typeitemactive').attr('data-type')
                    },

                    title: {
                        text: ''
                    },

                    subtitle: {
                        text: '',
                        style: {
                            display: 'none'
                        }
                    },

                    legend: {
                        enabled: false
                    },

                    plotOptions: {
                        spline: {
                            marker: {
                                radius: 4,
                                lineColor: '#666666',
                                lineWidth: 1
                            }
                        }

                    },
                    xAxis: {
                        reversed: isRTL,
                        categories: xAxisCategories,
                        labels: {
                            autoRotation: false,
                            staggerLines: labelXAxisStep
                        }
                    },
                    yAxis: {
                        opposite: isRTL,
                        //tickInterval: 10000,
                        title: {
                            text: yAxisTitleText
                        },
                        plotLines: [{
                            value: 0,
                            width: 1,
                            color: '#808080'
                        }]
                    },
                    tooltip: {
                        headerFormat: '<span style="font-size: 12px;font-weight:bold;">' + usageFor + '</span><br><span style="color:{point.color}">\u25CF</span> {point.x} {series.name}: ',
                        pointFormat: '<b>{point.y}</b>',
                        valueSuffix: tooltipValueSuffix,
                        crosshairs: true,
                        useHTML: true
                    },

                    series: series
                };

            if (utils.breakpoint() === 's') {
                opts.xAxis.labels.staggerLines = labelXAxisStepMobile;
            }

            $container.highcharts(opts);

            this.$component.find('.m29v2-chart__legend--item').each(function() {
                $(this).find('.m29v2-chart__legend--item-icon').css('color', $(this).data('color'));
            });
        };

        return ChartV2;
    });

