define('../src/sublayouts/m28-dashboard-component/m28-dashboard-component',['jquery', 'chart-config'], function($) {

    'use strict';

    var DashboardComponent = function($component) {

        this.$component = $component;

        return this;
    };

    DashboardComponent.prototype.init = function() {

        this.chartData = this.$component.data('chart-data');
        this.colorDown = '#087B36';
        this.colorUp = '#0068BD';

        if (this.chartData) {
            this.plotChart();
        }
        this.plotChartUpdateClone();
    };


    DashboardComponent.prototype.DataConvert = function(data) {
        var intArray = [];
        if (data != null && data != undefined) {
            data = data.toString();
            data.split(',').map(function(chartData) {
                intArray.push(Number(chartData));
            });
        }
        return intArray;
    }


    DashboardComponent.prototype.plotChart = function() {

        var chartData = this.chartData,
            data = this.DataConvert(chartData),
            color = chartData[0] > chartData[chartData.length - 1] ? this.colorUp : this.colorDown;

        this.$chartContainer = this.$component.find('[data-chart-container]');

        this.$chartContainer.highcharts({
            tooltip: {
                text: '',
                style: {
                    display: 'none'
                }
            },
            title: {
                text: '',
                style: {
                    display: 'none'
                }
            },
            subtitle: {
                text: '',
                style: {
                    display: 'none'
                }
            },

            xAxis: {

                lineWidth: 0,
                minorGridLineWidth: 0,
                lineColor: 'transparent',
                minorTickLength: 0,
                tickLength: 0,
                labels: {
                    enabled: false
                },
                title: {
                    text: null
                },
                reversed: $('html').attr('dir') == "rtl"
            },
            yAxis: {
                gridLineWidth: 0,
                minorGridLineWidth: 0,
                labels: {
                    enabled: false
                },
                title: {
                    text: null
                },
                opposite: $('html').attr('dir') == "rtl"
            },

            series: [{
                showInLegend: false,
                data: data,
                color: color
            }]
        });

    };

    DashboardComponent.prototype.plotChartUpdateClone = function() {
        window.setTimeout(function() {
            dewaGlobal.updateDashboardChart();
        }, 1100);
    };
    return DashboardComponent;

});

