define('../src/sublayouts/m46-understanding-your-bill/m46-understanding-your-bill',['jquery', 'slick', 'lib/utils'], function ($, slick, utils) {

	'use strict';

	var M46_UNDERSTANDING_YOUR_BILL = function ($component) {

		this.$component = $component;

		return this;
	};

	/**
	 * Initialise component
	 */

	M46_UNDERSTANDING_YOUR_BILL.prototype.init = function () {
		this.$M46_slider = $('.m46-understanding-your-bill__carousel');

		if ($('.m46-understanding-your-bill__carousel-slide').length > 1) {
			var _this = this;

			// listen for carousel initialisation
			this.$M46_slider.on('init', function(event){
				_this.display();
			});

			// initialise the carousel
			this.$M46_slider.slick({
				arrows: true,
				dots: true,
				infinite: true,
				speed: 750,
				slidesToShow: 1,
				slidesToScroll: 1,
				pauseOnHover: false,
				nextArrow: '.m46-understanding-your-bill__carousel-button--next',
				prevArrow: '.m46-understanding-your-bill__carousel-button--prev',
				rtl: ($('html').attr('dir') == 'rtl')
			});
		} else {
			// only one image, do not initialise the carousel, just display the image
			this.display();
		}
	};

	M46_UNDERSTANDING_YOUR_BILL.prototype.display = function () {
		this.$M46_slider.css({
			top: 0,
			opacity: 1,
			position: 'relative'
		});
	}

	return M46_UNDERSTANDING_YOUR_BILL;
});
